import React, { useState } from "react";
import { Checkbox } from "../Checkbox/Checkbox";
import classes from "./FilterList.module.scss";

export const FilterList = props => {
  const { hiddenTickets, sortNonStop, sortStops } = props.sortTickets;

  const [transfers, setTransfers] = useState([
    { value: "–í—Å–µ", id: "checkedAll", isChecked: true },
    { value: "–ë–µ–∑ –ø–µ—Ä–µ—Å–∞–¥–æ–∫", id: "nonStop", isChecked: true },
    { value: "1 –ø–µ—Ä–µ—Å–∞–¥–∫–∞", id: 1, isChecked: true },
    { value: "2 –ø–µ—Ä–µ—Å–∞–¥–∫–∏", id: 2, isChecked: true },
    { value: "3 –ø–µ—Ä–µ—Å–∞–¥–∫–∏", id: 3, isChecked: true }
  ]);

  /* —É–Ω–∏–≤–µ—Ä—Å–∞–ª—å–Ω–∞—è —Ñ—É–Ω–∫—Ü–∏—è –∫–æ—Ç–æ—Ä–æ–π –±—É–¥–µ–º –ø–µ—Ä–µ–∫–ª—é—á–∞—Ç—å —á–µ–∫–±–æ–∫—Å—ã */
  const checkID = (id, isChecked) => {
    if (isChecked) {
      const newChecked = transfers.map(item =>
        item.id === id ? { ...item, isChecked: true } : { ...item }
      );
      setTransfers(newChecked);
    } else {
      const newChecked = transfers.map(item => {
        if (item.id === id || item.id === "checkedAll") {
          return { ...item, isChecked: false };
        } else {
          return { ...item };
        }
      });
      setTransfers(newChecked);
    }
  };

  const changeIsChecked = (id, isChecked) => {
    // –µ—Å–ª–∏ –≤—ã–±—Ä–∞–Ω—ã "–≤—Å–µ" –±–∏–ª–µ—Ç—ã, –º–µ–Ω—è–µ–º –∏—Ö isChecked
    if (id === "checkedAll") {
      if (isChecked === true) {
        const newChecked = transfers.map(item => ({
          ...item,
          isChecked: true
        }));
        setTransfers(newChecked);
      } else {
        const newChecked = transfers.map(item => ({
          ...item,
          isChecked: false
        }));
        setTransfers(newChecked);
      }
      hiddenTickets(isChecked);
    }

    // –µ—Å–ª–∏ –≤—ã–±—Ä–∞–Ω–æ '–ë–µ–∑ –ø–µ—Ä–µ—Å–∞–¥–æ–∫'
    else if (id === "nonStop") {
      checkID(id, isChecked);
      sortNonStop(isChecked);
    }
    // –≤—Å–µ –æ—Å—Ç–∞–ª—å–Ω—ã–µ –≤–∞—Ä–∏–∞–Ω—Ç—ã –ø–µ—Ä–µ—Å–∞–¥–æ–∫
    else {
      checkID(id, isChecked);
      sortStops(id, isChecked);
    }
  };

  return (
    <div className={classes.FilterList}>
      <h2 className={classes.Title}>–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –ø–µ—Ä–µ—Å–∞–¥–æ–∫</h2>
      <ul>
        {transfers.map(item => {
          return (
            <li key={item.id}>
              <Checkbox item={item} checked={changeIsChecked} />
            </li>
          );
        })}
      </ul>
    </div>
  );
};
