import React, { useState, useEffect } from "react";
import classes from "./Search.module.scss";
import { FilterList } from "../components/FilterList/FilterList";
import { TicketList } from "../components/TicketList/TicketList";
import { Sorting } from "../components/Sorting/Sorting";
import { Alert } from "../components/Alert/Alert";

export const Search = () => {
  const [tickets, setTickets] = useState([]); // –Ω–µ–æ—Ç—Å–æ—Ä—Ç–∏—Ä–æ–≤–∞–Ω–Ω—ã–π —Å–ø–∏—Å–æ–∫ –±–∏–ª–µ—Ç–æ–≤
  const [hasError, setHasError] = useState(false);
  const [cloneTickets, setCloneTickets] = useState(null); // –º–∞—Å—Å–∏–≤ –±–∏–ª–µ—Ç–æ–≤ –∫–æ—Ç–æ—Ä—ã–π –±—É–¥–µ–º —Å–æ—Ä—Ç–∏—Ä–æ–≤–∞—Ç—å –ø–æ –ø–µ—Ä–µ—Å–∞–¥–∫–∞–º
  const [selectTub, setSelectTub] = useState("low price"); // —Ç–∞–± –¥–ª—è –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–æ–π —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏ –±–∏–ª–µ—Ç–æ–≤ (—Ü–µ–Ω–∞/—Å–∫–æ—Ä–æ—Å—Ç—å)

  // –æ—Ç—Å–æ—Ä—Ç–∏—Ä–æ–≤–∞–Ω–Ω—ã–µ –ø–æ –≤—Å–µ–º –ø–∞—Ä–∞–º–µ—Ç—Ä–∞–º –±–∏–ª–µ—Ç—ã –¥–ª—è –æ—Ç—Ä–∏—Å–æ–≤–∫–∏
  const [sortTickets, setSortTickets] = useState(null);

  useEffect(() => {
    fetch("https://front-test.beta.aviasales.ru/search")
      .then(res => res.json())
      .then(({ searchId }) =>
        fetch(
          `https://front-test.beta.aviasales.ru/tickets?searchId=${searchId}`
        )
      )
      .then(res => res.json())
      .then(({ tickets }) => {
        setTickets(tickets.slice(0, 5));
        setCloneTickets(tickets.slice(0, 5));
      })
      .catch(() => setHasError(true));
  }, []);

  useEffect(() => {
    function setSortTabs() {
      if (!cloneTickets) return;
      if (selectTub === "low price") {
        const newArr = [...cloneTickets].sort((a, b) =>
          a.price > b.price ? 1 : -1
        );
        setSortTickets(newArr);
      }
      if (selectTub === "fastest") {
        const newArr = [...cloneTickets].sort((a, b) => {
          const firstTicket = a.segments.reduce(
            (acc, i) => i.duration + acc,
            0
          );
          const secondTicket = b.segments.reduce(
            (acc, i) => i.duration + acc,
            0
          );

          if (firstTicket > secondTicket) {
            return 1;
          } else if (firstTicket < secondTicket) {
            return -1;
          } else {
            return 0;
          }
        });
        setSortTickets(newArr);
      }
    }
    setSortTabs();
  }, [cloneTickets, selectTub]);

  // —Å–∫—Ä—ã—Ç—å/–ø–æ–∫–∞–∑–∞—Ç—å –≤—Å–µ –±–∏–ª–µ—Ç—ã
  const hiddenTickets = isChecked => {
    isChecked ? setCloneTickets(tickets) : setCloneTickets([]);
  };

  // –±–∏–ª–µ—Ç—ã –±–µ–∑ –ø–µ—Ä–µ—Å–∞–¥–æ–∫
  const sortNonStop = isChecked => {
    if (isChecked) {
      const newArr = tickets.filter(item => {
        return item.segments.every(i => i.stops.length === 0); // every –≤–µ—Ä–Ω–µ—Ç true, –µ—Å–ª–∏ –í–°–ï –º–∞—Ä—à—Ä—É—Ç—ã –±–µ–∑ –ø–µ—Ä–µ—Å–∞–¥–æ–∫
      });
      setCloneTickets([...cloneTickets, ...newArr]); // –≤–µ—Ä–Ω–µ–º –≤ state –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤ —Å –±–∏–ª–µ—Ç–∞–º–∏ –±–µ–∑ –ø–µ—Ä–µ—Å–∞–¥–æ–∫
    } else {
      const newArr = cloneTickets.filter(item => {
        return item.segments.some(i => i.stops.length > 0); // –æ—Å—Ç–∞–≤–∏–º —Ç–æ–ª—å–∫–æ —Ç–µ –±–∏–ª–µ—Ç—ã, —É –∫–æ—Ç–æ—Ä—ã—Ö —Ö–æ—Ç—è –±—ã –æ–¥–∏–Ω –º–∞—Ä—à—Ä—É—Ç —Å –ø–µ—Ä–µ—Å–∞–¥–∫–æ–π
      });
      setCloneTickets(newArr);
    }
  };

  /* —É–Ω–∏–≤–µ—Ä—Å–∞–ª—å–Ω–∞—è —Ñ—É–Ω–∫—Ü–∏—è –∫–æ—Ç–æ—Ä–æ–π –±—É–¥–µ–º —Å–æ—Ä—Ç–∏—Ä–æ–≤–∞—Ç—å –±–∏–ª–µ—Ç—ã 
    –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ –ø–µ—Ä–µ—Å–∞–¥–æ–∫ */
  const sortStops = (numb, isChecked) => {
    if (isChecked) {
      const newArr = tickets.filter(item => {
        // –≤–µ—Ä–Ω–µ—Ç 'true' –µ—Å–ª–∏ —Ö–æ—Ç—è –±—ã –æ–¥–∏–Ω –º–∞—Ä—à—Ä—É—Ç –≤ –±–∏–ª–µ—Ç–µ –±—É–¥–µ—Ç —Å –∑–∞–¥–∞–Ω–Ω—ã–º —á–∏—Å–ª–æ–º –ø–µ—Ä–µ—Å–∞–¥–æ–∫
        const sort1 = item.segments.some(i => i.stops.length === numb);
        // –≤–µ—Ä–Ω–µ—Ç 'true' –µ—Å–ª–∏ –∫–∞–∂–¥—ã–π –º–∞—Ä—à—Ä—É—Ç –º–µ–Ω—å—à–µ –∏–ª–∏ —Ä–∞–≤–Ω—è–µ—Ç—Å—è –∑–∞–¥–∞–Ω–Ω–æ–º—É —á–∏—Å–ª—É –ø–µ—Ä–µ—Å–∞–¥–æ–∫
        const sort2 = item.segments.every(i => i.stops.length <= numb);
        return sort1 && sort2;
      });
      setCloneTickets([...cloneTickets, ...newArr]);
    } else {
      const newArr = cloneTickets.filter(item => {
        const sort1 = item.segments.some(i => i.stops.length > numb);
        const sort2 = item.segments.every(i => i.stops.length < numb);
        return sort1 || sort2;
      });
      setCloneTickets(newArr);
    }
  };

  // —Ñ—É–Ω–∫—Ü–∏—è –¥–ª—è –∏–∑–º–µ–Ω–µ–Ω–∏—è state 'selectTub' –¥–ª—è —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏ –ø–æ —Ç–∞–±–∞–º
  const sortTabs = id => {
    setSelectTub(id);
  };

  return (
    <div className={classes.appContentInner}>
      <div className={classes.appInformer}>
        <FilterList sortTickets={{ hiddenTickets, sortNonStop, sortStops }} />
      </div>
      <div className={classes.appContent}>
        <div className={classes.sorting}>
          <Sorting sorts={{ sortTabs }} />
        </div>
        {sortTickets ? (
          <TicketList tickets={sortTickets} />
        ) : hasError ? (
          <Alert />
        ) : null}
      </div>
    </div>
  );
};
